<template>
    <div class="layout df" id="layout">
        <!-- layoutheader -->
        <layoutheader @logout="logout"></layoutheader>
        <!-- layout-menu -->
        <div class="layout-menu">
            <layoutmenu :menus="menus"></layoutmenu>
        </div>
        <!-- layout-content -->
        <div class="layout-content flex1">
            <div class="bream-nav"></div>
            <div class="inner">
            <keep-alive include="v2BuildOrder">
                <router-view></router-view>
            </keep-alive>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex';
import { setUserInfo, setPageMenu, setFuncMenu } from '@/store/mutation-types.js';
import { array2tree } from '@/utils/util'
import layoutheader from './header';
import layoutmenu from './menu';
import MENUS from './menus'
//import * as API from '@/api/userApi';

export default {
    name: 'layout',
    data: function() {
        return {
            menus: [], //IS_DEV ? [...MENUS, ...TEST_MENUS] : Object.freeze(MENUS),
        }
    },
    components: {
        layoutheader,
        layoutmenu
    },
    methods: {
        ...mapMutations({ setUserInfo: setUserInfo }),
        // header methods
        logout() {
            // 清除浏览器存储的用户数据
            // 清除store 用户信息
            this.setUserInfo({})
            // 重定到登陆
            // this.$router.replace({ path: '/login' })
        },
        // 加载菜单
        loadUserMenu() {
            //let params = { act_user_id: this.userInfo.user_id }
            this.menus = MENUS
        }
    },
    computed: {
      
    },
    created() {
        this.loadUserMenu();
    }
}
</script>
<style lang="scss" src="./style.scss"></style>



