<template>
    <div id="layoutHeader" class="layout-header clearfix">
        <div class="fl logo" :class="[isCollaps ? 'logo-slide' : '']">
            <div>阿古电务</div>
        </div>
        <div class="fl top-menu">
            <el-menu
                :default-active="activeIndex2"
                class="el-menu-demo"
                mode="horizontal"
                text-color="#fff"
                active-text-color="#0099CC">
                <el-menu-item index="1"></el-menu-item>
                <el-menu-item index="2">资产管理</el-menu-item>
                <el-menu-item index="3">运维管理</el-menu-item>
                <el-menu-item index="4">知识库</el-menu-item>
                <el-menu-item index="5">数据统计</el-menu-item>
                <el-menu-item index="6">系统管理</el-menu-item>
            </el-menu>
        </div>
        
        <div class="fr-info">
            <el-badge :is-dot="false" class="item mr30 mt5 pointer">
                <img src="../../assets/icons/message-icon.png">
            </el-badge>
            <div class="user-info ml10" @click="toggleUserInfo" v-clickoutside="closeUserInfo">
                <div class="user-photo">
                    <img :src="userInfo.pic_path || defaultAvatar" alt="" >
                </div>
                
                <span class="user-name">王力宏</span>
                <div class="user-panel" v-show="showUserInfo" @click.stop="">
                    <ul>
                        <li>个人设置</li>
                        <li>切换企业</li>
                        <li>帮助中心</li>
                        <li>提交建议反馈</li>
                        <li class="breakup-line">在微信上处理</li>
                        <li>下载APP应用</li>
                        <li  @click="logout">安全退出</li>
                    </ul>
                </div>
            </div>
            
        </div>
        
    </div>
</template>
<script>
    import {mapState} from 'vuex'
    import clickoutside from '@/directives/clickoutside'
    import defaultAvatar from '@/assets/icons/avatar.png'
    export default {
        name: 'layoutHeader',
        data (){
            return {
                keyword: '',
                packageUrl: '',
                isWindow: true,
                showUserInfo: false,
                defaultAvatar: defaultAvatar,
                isCollaps: false,
                activeIndex2: '1',
                collapsLogo:'logo-slide'
            } 
        },
        directives: {clickoutside},
        computed: {
            ...mapState(['userInfo']),
        },
        methods: {
            logout (){
                this.$emit('logout');
            },
            toggleUserInfo (){
                this.showUserInfo = !this.showUserInfo
            },
            closeUserInfo (){
                if (this.showUserInfo) {
                    this.showUserInfo = false
                }
            },
          
            
        },
        created (){
            this.isWindow = navigator.platform.toLowerCase().indexOf('macintel') == -1;
        },
        mounted (){
            bus.$on('collaps-change',(boolean)=>{
                this.isCollaps = boolean
            })
        },
    }
</script>
<style scoped lang="scss">
    @import "../../scss/helper/_variable.scss";
    
</style>


