<template>
    <el-menu
        class="el-menu-vertical-demo"
        text-color="#fff"
        :router="true"
        :collapse="isCollapse"
        :class="[isCollapse?'meun-collapse':'']"
        :default-active="''+activeIndex"
        :default-openeds="openParent">
        <div class="menu-controller" @click="collaps">
            <img v-if="!isCollapse" src="../../assets/icons/spread.png">
            <img v-else="" src="../../assets/icons/shrink.png">
        </div>
        <template v-for="item in menus">
            <el-submenu v-if="item.children && item.children.length>0" :index="item.id" :key="item.id">
                <template slot="title">
                    <i :class="'el-icon-'+(item.icon?item.icon:'menu')"></i>
                    <span>{{item.title}}</span>
                </template>
                <el-menu-item :key="children.id" v-for="children in item.children" :index="children.id"  :route="{path: children.path}">{{children.title}}</el-menu-item>
            </el-submenu>
            <el-menu-item v-else :index="item.id" :route="{path: item.path}" :key="item.id">
                <i :class="'el-icon-'+(item.icon?item.icon:'menu')"></i>
                <span slot="title">{{item.title}}</span>
            </el-menu-item>
        </template>
    </el-menu>
</template>

<script>
export default {
  name: "sidebar",
  data() {
    return {
      leaf: Object.freeze(null),
      leafs: Object.freeze([]),
      index: "1",
      isCollapse: false,
      activeIndex: "0",
      openParent:[]
    };
  },
  props: {
    menus: {
      required: true,
      type: Array
    },
    theme: {
      type: String,
      default: "dark"
    }
  },
  watch: {},
  computed: {},
  created() {
    this.getRouteInfo(this.menus, this.$route.path);
  },
  mounted() {

  },
  methods: {
    getRouteInfo(arr, path) {
      arr.forEach((v, i) => {
        if (v.children) {
          this.getRouteInfo(v.children, path);
        } else if (path == v.path) {
          this.activeIndex = "" + v.id;
          if (v.pid != 0 && this.openParent.includes(v.pid)) {
            this.openParent.push(v.pid);
          }
        }
      });
    },
    collaps() {
      this.isCollapse = !this.isCollapse;
      bus.$emit("collaps-change", this.isCollapse);
    }
  }
};
</script>



